
body {
  margin: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
html, body, #app {
  height: 100%;
  overflow: hidden;
}
.md-fixed {
  position: fixed;
  bottom: 0;
}
.page {
    padding: 8px 16px;
    overflow: auto;
    height: 100%;
}
.main-title {
    padding: 10px 15px;
}



